@charset "utf-8";
@use "global" as *;

.pplcy__expl {
  // sm first
  font-family: "游ゴシック体";
  font-size: 0.875rem;
  font-weight: $regular;
  letter-spacing: 0rem;
  line-height: 1.7;
  color: #333;

  // tablet 768px
  @include mq(md) {
    font-size: 1.32rem;
    letter-spacing: 0.11rem;
    line-height: 1.63;
  }

  // pc 1440px
  @include mq(xl) {
    font-family: "Noto Sans JP";
    font-size: 1rem;
    letter-spacing: 0.002rem;
    line-height: 1.49;
  }
}
